<template>
  <div class="Address">
    <appHerder :ifback='true' title='选择收货地点' go='/home'></appHerder>
    <div class="search">
      <button @click="$router.push('/city')">{{city}}<i class="fa fa-angle-down"></i></button><input type="text" v-model="search">
    </div>
    <div class="nowress"><i class="fa fa-send"></i>当前地址：{{nowress}}</div>
    <div ref="address" class="addressdata">
    <ul>
      <li v-for='(value,index) in data' :key="index" @click="address(value)"  v-show='value.district'>
        <h3>{{value.name}}</h3>
        <p>{{value.district}}{{value.address}}</p>
      </li>
    </ul></div>
    </div>
</template>

<script>
import Header from '../components/Header.vue'
import BScroll from 'better-scroll'
export default {
  name: 'Address',
  data(){
  	return {
  		city:'',
      nowress:'',
      search:'',
      data:[],
      scroll:null
  	}
  },
  components:{
  	appHerder:Header
  },
  created(){
  	// console.log(this.$store.getters.location);
  	this.city = this.$store.getters.location.addressComponent.city||
        this.$store.getters.location.addressComponent.province;
    this.nowress = this.$store.getters.address;
    
  },
  methods:{
    address(value){
      if(value.district){
         this.$store.dispatch("setAddress",value.district+value.address);
      }else{
         this.$store.dispatch("setAddress",value.name);
      }
      this.$router.push('/home');

    },
    initScroll(){
       this.scroll = new BScroll(this.$refs.address,{
          click:true
       });
    }
  },
  mounted(){
    this.initScroll();

  },
  watch:{
    search(){
      let $this = this;
      AMap.plugin('AMap.Autocomplete', function(){
      // 实例化Autocomplete
      var autoOptions = {
        //city 限定城市，默认全国
        city: $this.city
      }
      var autoComplete= new AMap.Autocomplete(autoOptions);
      autoComplete.search($this.search, function(status, result) {
        // console.log(result);
        $this.data = result.tips;
      })
    })
    }

  }
}
</script>


<style scoped>
.Address{
  height: 100%;
  overflow: hidden;
}
.search{
  width: 100%;
  height:2rem;
  background-color: #fff;
  line-height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search button{
  width: 23%;
  height: 1.4rem;
  border:none;
  line-height: 1.4rem;
  box-sizing: border-box;
  border-top-left-radius: 10px;
  background-color: #e3e3e3;
  border-bottom-left-radius: 10px;
  font-size: 0.6rem;
}
.search input{
  width: 73%;
  height: 1.4rem;
  line-height: 1.4rem;
  border:none;
  box-sizing: border-box;
  background-color: #e3e3e3;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding-left: 1em;
  font-size: 0.6rem;
}
.nowress{
  margin-top: 4px;
  background-color: #fff;
  height: 2.4rem;
  line-height: 1.2rem;
  overflow: hidden;
}
.nowress i{
  color: blue;
  margin: 0 7px;
}
.addressdata{
  height: calc(100% - 160px);
  overflow: hidden;

}
.addressdata li{
  list-style: none;
  background-color: #fff;
  /*height: 65px;*/
  margin-top: 5px;
  padding-left: 10px;
  padding-top: 5px;
  box-sizing:border-box;
}
.addressdata li p{
  padding: 5px 0;
}
</style>